/* Global styles */
body {
  margin: 0;
  font-family: 'Roboto', 'Helvetica Neue', Arial, sans-serif; /* Material Design font */
  line-height: 1.6; /* Improved readability */
  color: #333; /* Darker text color for better contrast */
  background-color: #f9f9f9; /* Light background for a softer appearance */
  -webkit-font-smoothing: antialiased; /* Smoother font rendering on WebKit */
  -moz-osx-font-smoothing: grayscale; /* Smoother font rendering on macOS */
}

/* Code styles */
code {
  font-family: 'Source Code Pro', Menlo, Monaco, Consolas, 'Courier New', monospace; /* Specify primary font */
  background-color: #f4f4f4; /* Light background for code snippets */
  color: #d63384; /* Bright color for better visibility */
  padding: 2px 4px; /* Padding for spacing around code */
  border-radius: 4px; /* Rounded corners for a modern look */
  font-size: 0.95em; /* Slightly smaller font size for inline code */
}

/* Preformatted text */
pre {
  background-color: #2a2a2a; /* Dark background for preformatted text */
  color: #f8f8f2; /* Light text color for contrast */
  padding: 16px; /* Padding for spacing */
  border-radius: 4px; /* Rounded corners */
  overflow-x: auto; /* Allow horizontal scrolling for long lines */
  white-space: pre-wrap; /* Wrap lines within the container */
  margin: 1em 0; /* Margin for spacing around code blocks */
}

/* Responsive font size adjustments */
@media (max-width: 768px) {
  body {
    font-size: 16px; /* Larger base font size for mobile devices */
  }

  code {
    font-size: 0.9em; /* Adjust code font size for better fit */
  }
}